import { useState } from "react";

import Button from "../Buttons/Button";

function ApplicationForm({ history, strsForRender, onSubmit }) {
  const [formData, setFormData] = useState({ recipient: "", fio: "", address: "", tel: "", date: "" });

  const handleChange = (e) => {
    const { name, value } = e.target;
    const cloneFormData = { ...formData };
    cloneFormData[name] = value;
    setFormData(cloneFormData);
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    const clone = { ...formData };
    clone["strings"] = strsForRender;
    onSubmit(clone);
    history.push("/petition-created");
  };

  let date = new Date();
  const currentDay = `${date.getFullYear()}-${date.getMonth() + 1}-${date.getDate()}`;

  console.log(currentDay)
  return (
    <form name="bioForm" className="form-bio" onSubmit={handleSubmit}>
      <div className="form-bio__data">
        <h2 className="title form-bio__title">–û—Ñ–æ—Ä–º–∏—Ç–µ –¥–æ–∫—É–º–µ–Ω—Ç</h2>
        <fieldset className="fieldset form-bio__fieldset">
          <h3 className="title title_size_medium form-bio__title">–í–≤–µ–¥–∏—Ç–µ –∞–¥—Ä–µ—Å–∞—Ç–∞</h3>
          <textarea
            className="form-bio__recipient"
            placeholder="–í–æ–ª–∫–æ–≤—É –ï–≤–≥–µ–Ω–∏—é –ù–∏–∫–æ–ª–∞–µ–≤–∏—á—É, –≥—Ä–∞–¥–æ–Ω–∞—á–∞–ª—å–Ω–∏–∫—É –≥. –ú–æ—Å–∫–≤–∞"
            name="recipient"
            onChange={handleChange}
            value={formData[`recipient`]}
          ></textarea>
        </fieldset>
        <fieldset className="fieldset form-bio__fieldset">
          <h3 className="title title_size_medium form-bio__title">–í–≤–µ–¥–∏—Ç–µ –≤–∞—à–∏ –¥–∞–Ω–Ω—ã–µ</h3>
          <label htmlFor="fio" className="form-bio__label">
            –§–ò–û*
          </label>
          <input
            type="text"
            name="fio"
            id="fio"
            placeholder="–ú–∞—è–∫–æ–≤—Å–∫–∏–π –í–ª–∞–¥–∏–º–∏—Ä –í–ª–∞–¥–∏–º–∏—Ä–æ–≤–∏—á"
            className="form-bio__input"
            onChange={handleChange}
            value={formData[`fio`]}
          />
          <label htmlFor="address" className="form-bio__label">
            –ê–¥—Ä–µ—Å*
          </label>
          <input
            type="text"
            name="address"
            id="address"
            placeholder="–£–ª–∏—Ü–∞, –¥–æ–º, –≥–æ—Ä–æ–¥, –æ–±–ª–∞—Å—Ç—å"
            className="form-bio__input"
            onChange={handleChange}
            value={formData[`address`]}
          />
          <label htmlFor="tel-number" className="form-bio__label">
            –¢–µ–ª–µ—Ñ–æ–Ω*
          </label>
          <input
            type="tel"
            name="tel"
            id="tel-number"
            placeholder="+7 XXX XXX-XX-XX"
            className="form-bio__input"
            onChange={handleChange}
            value={formData[`tel`]}
          />
          <label htmlFor="date" className="form-bio__label">
            –î–∞—Ç–∞ –æ–±—Ä–∞—â–µ–Ω–∏—è
          </label>
          <input
            type="date"
            name="date"
            id="date"
            placeholder="–î–î.–ú–ú.–ì–ì–ì–ì"
            className="form-bio__input"
            onChange={handleChange}
            value={formData[`date`] || currentDay}
          />
        </fieldset>
        <p className="form-bio__info">
          –≠–ª–µ–∫—Ç—Ä–æ–Ω–Ω—ã–π –¥–æ–∫—É–º–µ–Ω—Ç –±—É–¥–µ—Ç –¥–æ—Å—Ç—É–ø–µ–Ω –¥–ª—è —Ä–∞—Å–ø–µ—á–∞—Ç–∫–∏ –∏ —Ä–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞–Ω–∏—è –≤ –ª–∏—á–Ω–æ–º –∫–∞–±–∏–Ω–µ—Ç–µ.
        </p>
      </div>
      <div className="form-bio__preview">
        <div className="preview">
          <div className="preview__top">
            <p className="preview__recipient">
              {formData[`recipient`] || "–í–æ–ª–∫–æ–≤—É –ï–≤–≥–µ–Ω–∏—é –ù–∏–∫–æ–ª–∞–µ–≤–∏—á—É, –≥—Ä–∞–¥–æ–Ω–∞—á–∞–ª—å–Ω–∏–∫—É –≥. –ú–æ—Å–∫–≤–∞"}
            </p>
            <p className="preview__sender">–æ—Ç {formData[`fio`] || "–ú–∞—è–∫–æ–≤—Å–∫–æ–≥–æ –í–ª–∞–¥–∏–º–∏—Ä–∞ –í–ª–∞–¥–∏–º–∏—Ä–æ–≤–∏—á–∞"}</p>
            <p className="preview__adress">–ê–¥—Ä–µ—Å:{formData[`address`]}</p>
            <p className="preview__tel">–¢–µ–ª–µ—Ñ–æ–Ω:{formData[`tel`]}</p>
          </div>
          <div className="preview__body">
            <p className="preview__title">–û–±—Ä–∞—â–µ–Ω–∏–µ</p>
            <div className="preview__main-text">
              {strsForRender.map((item, index) => {
                return (
                  <p className="preview__stroke" key={index}>
                    {item}
                  </p>
                );
              })}
            </div>
          </div>
          <div className="preview__footer">
            <p className="preview__date">{formData[`date`] || currentDay}</p>
            <p className="preview__signature">/ –ø–æ–¥–ø–∏—Å—å –∑–∞—è–≤–∏—Ç–µ–ª—è</p>
          </div>
        </div>
        <Button type="submit" name="–û—Ç–ø—Ä–∞–≤–∏—Ç—å" element="application__control btn_size_m" />
      </div>
    </form>
  );
}

export default ApplicationForm;
