import mayak from "../../images/hero-mayak.png";
import { tutorialCards, actualCards, doneCards } from "../../utils/constants";

import Button from "../Buttons/Button";
import DoneCard from "./DoneCard";
import ActualCard from "./ActualCard";
import TutorialCard from "./TutorialCard";

function MainPage({ history, reset }) {
  const handlerReset = () => {
    reset();
    history.push("/tags-selection");
  };

  return (
    <>
      <section className="hero">
        <div className="hero__content">
          <h1 className="title hero__title">–°–∞–π—Ç –≥—Ä–∞–∂–¥–∞–Ω—Å–∫–∏—Ö –∏–Ω–∏—Ü–∏–∞—Ç–∏–≤</h1>
          <Button type="button" name="–°–æ–∑–¥–∞—Ç—å –∏–Ω–∏—Ü–∏–∞—Ç–∏–≤—É" element="hero__button" onClick={handlerReset} />
        </div>
        <div className="hero__diagonal-box">
          <div className="hero__phrase"></div>
        </div>
        <img src={mayak} alt="–ú–∞—è–∫–æ–≤—Å–∫–∏–π —Å –≤—ã—Ç—è–Ω—É—Ç–æ–π —Ä—É–∫–æ–π" className="hero__mayak" />
      </section>

      <section className="how-it-works">
        <div className="how-it-works__content">
          <h2 className="title how-it-works__title">–ö–∞–∫ —ç—Ç–æ —Ä–∞–±–æ—Ç–∞–µ—Ç</h2>
          <ul className="how-it-works__cards">
            {tutorialCards.map((item, index) => {
              return <TutorialCard item={item} key={index} />;
            })}
          </ul>
        </div>
      </section>

      <section className="actual">
        <div className="actual__diagonal-box"></div>
        <div className="actual__content">
          <h2 className="title actual__title">–ê–∫—Ç—É–∞–ª—å–Ω—ã–µ –∏–Ω–∏—Ü–∏–∞—Ç–∏–≤—ã</h2>
          <ul className="actual__cards">
            {actualCards.map((item, index) => {
              return <ActualCard item={item} key={index} />;
            })}
          </ul>
          <Button
            type="button"
            element="actual__btn"
            name="–°–º–æ—Ç—Ä–µ—Ç—å –µ—â—ë"
            onClick={() => alert("–°—Ç—Ä–∞–Ω–∏—Ü–∞ —Å –∞–∫—Ç—É–∞–ª—å–Ω—ã–º–∏ –∏–Ω–∏—Ü–∏–∞—Ç–∏–≤–∞–º–∏")}
          />
        </div>
      </section>

      <section className="done">
        <div className="done__diagonal-box"></div>
        <div className="done__content">
          <h2 className="title done__title">–£–∂–µ –ø—Ä–∏–Ω—è—Ç—ã</h2>
          <ul className="done__cards">
            {doneCards.map((item, index) => {
              return <DoneCard item={item} key={index} />;
            })}
          </ul>
          <Button
            type="button"
            element="done__btn"
            name="–°–º–æ—Ç—Ä–µ—Ç—å –µ—â—ë"
            onClick={() => alert("–°—Ç—Ä–∞–Ω–∏—Ü–∞ —Å –ø—Ä–∏–Ω—è—Ç—ã–º–∏ –∏–Ω–∏—Ü–∏–∞—Ç–∏–≤–∞–º–∏")}
          />
        </div>
      </section>
    </>
  );
}

export default MainPage;
